<template>
  <div id="app">
    <div class="topbar">
      <div id="scroll-line-yellow" class="scroll-line"></div>
      <!-- <div id="scroll-line-blue" class="scroll-line"></div> -->
    </div>
    <router-view />

    <!-- TODO: change the following nav into a new nav component -->

    <Footer></Footer>
  </div>
</template>

<script>
// @ is an alias to /src
import Footer from "@/components/Footer.vue";
import $ from "jquery";
import velocity from "velocity-animate";

export default {
  name: "App",
  components: {
    Footer
  },
  data() {
    return {
      scrolled: false
    };
  },
  methods: {
    vh(v) {
      var h = Math.max(
        document.documentElement.clientHeight,
        window.innerHeight || 0
      );
      return (v * h) / 100;
    },

    handleScroll() {
      var wintop = $(window).scrollTop(),
        docheight = $(document).height(),
        winheight = $(window).height();
      var scrolled = (wintop / (docheight - winheight)) * 100;
      $(".scroll-line").css("width", scrolled + "%");
    }
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  }
};
</script>

